ment servi comme Directeur du Département Formation de l’ONG IRESA, Directeur des Ressources Humaines de IFA et Directeur des Projets Afrique de TBEA, une des plus grandes Entreprises Chinoises et Mondiales en terme de Production / transmission / distribution de l’Energie. Ce qui lui a permis d’avoir un carnet d’adresse bien rempli.\n                           ")])])]),_c('div',{staticClass:"column"},[_c('div',{staticClass:"ui button basic "},[_c('i',{staticClass:"ui icon tag"}),_vm._v(" Pourquoi bk-group ?\n                          ")]),_c('br'),_c('br'),_c('div',{staticClass:"uk-text-justify"},[_c('p',[_vm._v("\n    Homme de vision, Monsieur BROU Koua Bertin, décide de mettre sur place une Entreprise en 2017 avec trois missions capitales.\n\n\n")]),_c('p',[_vm._v("\n    1/ La première mission était de trouver les meilleures Universités Chinoises afin d’aider les Etudiants Africains à faire les études dans ces Universités et dans de meilleures conditions. Monsieur BROU Koua Bertin va alors signer un partenariat avec l’agence d’Admission Chinoise appelée SICAS China d’où il devient le Représentant légal en Côte d’ivoire.\n")]),_c('p',[_vm._v("\n    2/ La deuxième mission était d’aider son pays en y introduisant des Investisseurs Chinois pour le développement des projets dans le domaine de l’Electricité.\n")]),_c('p',[_vm._v("\n    3/ Pour la troisième mission, Monsieur BROU Koua Bertin a pensé à la classe la plus délaissée en instituant des formations insertions accélérées, lesquelles formations aideront les apprenants d’être installés automatiquement après leur formation théorique et pratique auprès des maitres artisans. Pour y parvenir, Monsieur BROU Koua Bertin va entreprendre des démarches auprès des structures étatiques telle que le FDFP et emploi Jeune pour avoir des agréments d’exercices. Aussi, il signera des conventions de partenariats avec la chambre des métiers pour la formation pratique de ses apprenants.\n")])])])])])}]

